
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
name: 'LotteryRes',
components: {
XHeader
},
data() {
return {
list: []
};
},
created() {
this.getList();
},
methods: {
async getList() {
this.$vux.loading.show({
text: 'Loading'
});
let res = await this.$http('/queryAllGameTypeResult');
console.log(res);
this.list = res.returnList;
this.$vux.loading.hide();
}

}
};



